.App {
    background-image: url("images/background.png");
    background-size: cover;
    box-sizing: border-box;
    min-height: 100vh;
    padding-bottom: 70px;
    position: relative;
}

.App main {
    max-width: 1024px;
    margin: 0 auto;
}

.App header {
    background-color: #1B1B1B;
    padding: 10px;
}

header h1 {
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
}

.App footer {
    background-color: #000;
    bottom: 0;
    box-sizing: border-box;
    padding: 10px 10px;
    position: absolute;
    width: 100%;
}

footer img {
    display: block;
    height: 30px;
    margin: 0 auto;
}

