.hotel-card {
    box-sizing: border-box;
    margin: 0 auto;
    padding: 20px 0;
    width: 90%;
}

.hotel-card img {
    border: 2px solid #fff;
    box-sizing: border-box;
    box-shadow: 0px 1px 6px 1px rgba(0,0,0,0.78);
    -webkit-box-shadow: 0px 1px 6px 1px rgba(0,0,0,0.78);
    width: 100%;
}

.hotel-card h2 {
    color: #fff;
    display: block;
    font-size: 18px;
    font-weight: bold;
    padding: 10px 0;
}

.hotel-card address {
    color: #a3beff;
    line-height: 1.5em;
}

.hotel-card address span {
    display: block;
}

.hotel-card--phone {
    color: #fff;
    display: block;
    padding-top: 5px;
}

.hotel-card address,
.hotel-card--phone {
    font-size: .9em;
}
