body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hotel-card {
    box-sizing: border-box;
    margin: 0 auto;
    padding: 20px 0;
    width: 90%;
}

.hotel-card img {
    border: 2px solid #fff;
    box-sizing: border-box;
    box-shadow: 0px 1px 6px 1px rgba(0,0,0,0.78);
    -webkit-box-shadow: 0px 1px 6px 1px rgba(0,0,0,0.78);
    width: 100%;
}

.hotel-card h2 {
    color: #fff;
    display: block;
    font-size: 18px;
    font-weight: bold;
    padding: 10px 0;
}

.hotel-card address {
    color: #a3beff;
    line-height: 1.5em;
}

.hotel-card address span {
    display: block;
}

.hotel-card--phone {
    color: #fff;
    display: block;
    padding-top: 5px;
}

.hotel-card address,
.hotel-card--phone {
    font-size: .9em;
}

.menu-list--ul {
    border-radius: 10px;
    margin: 0 auto;
    overflow: hidden;
    width: 90%;
}

.menu-list--ul li,
.menu-list--ul button {
    display: block;
    width: 100%;
}

.menu-list--ul button {
    color: #1f374f;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
    padding: 10px;
    text-align: left;
}
.App {
    background-image: url(/hilton/static/media/background.0b2aa647.png);
    background-size: cover;
    box-sizing: border-box;
    min-height: 100vh;
    padding-bottom: 70px;
    position: relative;
}

.App main {
    max-width: 1024px;
    margin: 0 auto;
}

.App header {
    background-color: #1B1B1B;
    padding: 10px;
}

header h1 {
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
}

.App footer {
    background-color: #000;
    bottom: 0;
    box-sizing: border-box;
    padding: 10px 10px;
    position: absolute;
    width: 100%;
}

footer img {
    display: block;
    height: 30px;
    margin: 0 auto;
}


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
    font-family: Arial;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
