.menu-list--ul {
    border-radius: 10px;
    margin: 0 auto;
    overflow: hidden;
    width: 90%;
}

.menu-list--ul li,
.menu-list--ul button {
    display: block;
    width: 100%;
}

.menu-list--ul button {
    color: #1f374f;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
    padding: 10px;
    text-align: left;
}